<template>
  <div class="dashboard" style="position:absolute;top:55px">
    <div id="gaugeContainer" style=" width: 100%;height:750px;"></div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  name: "dashboard",
  data() {
    return {
       list: ["强卖出", "卖出", "观望", "买入", "强买入"]
    };
  },
  created() {
    const that = this;
    const myCharts = echarts.init(document.getElementById("gaugeContainer"));
    const option = {
      series: [
        {
          startAngle: 180,
          endAngle: 0,
          type: "gauge",
          radius: "100%",
          splitNumber: 10,
          axisLine: {
            // 坐标轴线
            lineStyle: {
              // 属性lineStyle控制线条样式
              width: 500,
              color: [[0, "rgba(0,0,0,0.3)"], [1, "rgba(0,0,0,0.3)"]],
              opacity: 0.8
            }
          },
          axisTick: { show: false },
          axisLabel: {
            // 坐标轴小标记
            show: false
          },
          splitLine: {
            // 分隔线
            show: false
          },
          pointer: {
            // 分隔线
            show: false
          },
          detail: {
            show: false
          },
          data: [{ value: 40 }]
        },
        {
          
          type: "gauge",
          startAngle: 180,
          endAngle: 0,
          splitNumber: 20,
          radius: "100%", //仪表大小
          axisLine: {
            lineStyle: {
              // 属性lineStyle控制线条样式
              color: [
                [
                  1,
                  new echarts.graphic.LinearGradient(
                    0,
                    0,
                    1,
                    0,
                    [
                      {
                        offset: 1,
                        color: "#FA5343" // 50% 处的颜色
                      },
                      {
                        offset: 0.1,
                        color: "#2FA37B " // 40% 处的颜色
                      }
                    ],
                    false
                  )
                ], // 100% 处的颜色
              ],
              width: 10
            }
          },
          markPoint: {
            symbol: "rect",
            symbolSize: [50, 20],
            emphasis: {
              label: {}
            },
            label: {
              color: "#fff",
              fontSize: 38,//
              lineHeight: 12,
              offset: [0, -15]
            },
            data: [
              //跟你的仪表盘的中心位置对应上，颜色可以和画板底色一样
              {
                x: "105",
                y: "center",
                itemStyle: { color: "transparent" },
                value: "强卖出"
              },
              {
                x: "980",
                y: "center",
                itemStyle: { color: "transparent" },
                value: "强买入"
              }
            ]
          },
          title: { show: true },
          detail: { show: false },
          splitLine: { show: false },
          axisTick: {
            show: false,
            splitNumber: 5,
            lineStyle: {
              color: "#52B8DF",
              width: 3
            }
          },
          axisLabel: {
            fontSize: 38,
            color: "#fff",
            distance: -110,
            formatter: function(value) {
              if (Number.isInteger(value / 25) && value != 0 && value != 100&& value != 50) {
                return that.list[value / 25];
              } else {
                return null;
              }
            }
          },
          pointer: {
            //指针宽度
            width: 7
          },
          itemStyle: {
            color: "#7F8FC8", //指针颜色
          },
          data: [
            {
              value: 90,
              name: "今日指数"
            }
          ]
        }
      ]
    };
    myCharts.setOption(option);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.dashboard{
    width: 100%;
    height: 100%
}
#gaugeContainer{
    
}
</style>
